import { Component, Input } from '@angular/core';

@Component({
  selector: 'bb-user-context-icon-ui',
  templateUrl: './user-context-icon.component.html',
})
export class UserContextIconComponent {
  @Input() isMaster: boolean | undefined;
  @Input() name: string | undefined;

  get formattedName(): string {
    if (this.name) {
      const short = this.name
        .split(/\s/)
        .reduce((r: string, w: string) => (r += w.slice(0, 1)), '')
        .substring(0, 2);
      return short.toLocaleUpperCase();
    }
    return '';
  }
}
